import { toggleTwoClasses, randomInteger, randomArray } from "./helpers"

export function animate() {
    let iteration = 0

    let pieces = 20

    let currentPack

    let animBtn = document.querySelector('#main-anim')
    let innerBtn = document.querySelector('#main-anim input')
    console.log(innerBtn)
    let animBtnInner = document.querySelector('#main-anim .input-submit__input')
    let animWrap = document.querySelector('.anim-wrapper')
    let animEls = document.querySelectorAll('.anim-img')
    let animBox = document.querySelector('.box-img')
    let animBoxBody = document.querySelectorAll('.box-img__body')

    let wrapAnimEl = document.querySelector('.index-create-gift__wrapper__bottom__image')
    let btnCatalog = document.querySelector('.index-create-gift__wrapper__bottom__button--second')

    function mainAnimate() {
        addConfeti()
        animateConfeti()
        randomPack()
        for (let i = 0; i < animEls.length; ++i) {
            animEls[i].classList.add(`animate${i}`)
        }
        animBox.classList.add('box-img__open')
        animBoxBody.forEach(el => el.classList.add('box-img__hidden'))
        wrapAnimEl.classList.add('open')
        animBtn.classList.add('open')
        animBtnInner.value = 'Другой подарок'
    }

    function addConfeti() {
        for (let i = 0; i < pieces; ++i) {
            let block = document.createElement('div')
            block.classList.add('confeti')
            animWrap.append(block)
            let side = randomArray(['left', 'right'])
            block.animate([
                // key frames
                {
                    [side]: '0',
                    transform: 'rotate(0deg) scale(0)',
                    opacity: '0',
                },
                {
                    top: `${randomInteger(-110, 110)}%`, // -40, 100
                    [side]: `${randomInteger(-50, -150)}%`, // -50, -120
                    transform: `rotate(${randomInteger(45, 45)}deg) scale(1)`,
                    opacity: '1',
                }
            ], {
                // sync options
                duration: 800,
                fill: "forwards",
            });
        }
    }

    function animateConfeti() {
        let animEls = document.querySelectorAll('.confeti')
        if (!animEls) return
        animEls.forEach(el => {
            let randomX = randomInteger(-50, 50);
            let randomY = randomInteger(-50, 50);
            let randomRotate = randomInteger(0, 45);
            console.log(randomRotate)
            el.animate([
                // key frames
                {
                    transform: `translate(${randomX}%, ${randomY}%) rotate(${randomRotate}deg)`,
                    opacity: 1,
                },
                {
                    transform: `translate(${randomInteger(-50, 50)}%, ${randomInteger(-50, 50)}%) rotate(${randomRotate}deg)`,
                    opacity: 0.8,
                },
                {
                    transform: `translate(${randomInteger(-50, 50)}%, ${randomInteger(-50, 50)}%) rotate(${randomRotate}deg)`,
                    opacity: 0.7,
                },
                {
                    transform: `translate(${randomInteger(-50, 50)}%, ${randomInteger(-50, 50)}%) rotate(${randomRotate}deg)`,
                    opacity: 0.7,
                },
                {
                    transform: `translate(${randomInteger(-50, 50)}%, ${randomInteger(-50, 50)}%) rotate(${randomRotate}deg)`,
                    opacity: 0.8,
                },
                {
                    transform: `translate(${randomX}%, ${randomY}%) rotate(${randomRotate}deg)`,
                    opacity: 1,
                },
            ], {
                // sync options
                duration: 5000,
                iterations: Infinity,
            });
        })
    }


    function randomPack() {
        let welcomePack = currentPack = document.getElementById(`pack-${randomInteger(1, 8)}`)
        console.log(welcomePack)
        console.log(currentPack)
        toggleTwoClasses(welcomePack, "is-visible", "is-hidden", 800)
    }

    function removeAnimate() {
        for (let i = 0; i < animEls.length; ++i) {
            animEls[i].classList.remove(`animate${i}`)
        }
        animBox.classList.remove('box-img__open')
        animBoxBody.forEach(el => el.classList.remove('box-img__hidden'))
        wrapAnimEl.classList.remove('open')
        animBtn.classList.remove('open')
        toggleTwoClasses(currentPack, "is-hidden", "is-visible", 800)
        removeConfeti()
    }

    function removeConfeti() {
        let confeti = document.querySelectorAll('.confeti')
        if (!confeti) return
        confeti.forEach(el => el.remove())
    }

    function disabledBtn() {
        innerBtn.disabled = false
    }

    if (!animBtn) return
    animBtn.addEventListener('click', () => {
        innerBtn.disabled = true
        console.log(iteration)
        if (iteration != 0) {
            wrapAnimEl.classList.add('fix-height')
            wrapAnimEl.classList.add('re-open')
            setTimeout(() => {
                removeAnimate()
            }, 400);
            setTimeout(() => {
                wrapAnimEl.classList.remove('re-open')
            }, 600);
            setTimeout(() => {
                mainAnimate()
            }, 800);

            if (iteration === 2) {
                toggleTwoClasses(btnCatalog, "is-visible", "is-hidden", 800)
            }
        }
        else {
            mainAnimate()
        }

        setTimeout(() => {
            disabledBtn()
        }, 800)

        iteration += 1
    })
}