<template>
<div class="container-wrapper">
<div class='container' ref="container">
    <div id="myProgress">
        <div ref="bar" id="myBar"></div>
    </div>

    <button ref="nextBtn" class="prev-button" data-cursor="focus" @click="swipe.slidePrev(), decreaseCount()">
        <svg width="15" height="10" viewBox="0 0 15 10" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M15 5H1.5M1.5 5L5.5 1M1.5 5L5.5 9" stroke="#E9178A" stroke-width="1.5" stroke-linejoin="bevel" />
        </svg>
    </button>
    <button class="next-button" data-cursor="focus" @click="swipe.slideNext(), increaseCount()">
        <svg width="15" height="10" viewBox="0 0 15 10" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M6.99382e-07 5L13.5 5M13.5 5L9.5 9M13.5 5L9.5 1" stroke="#E9178A" stroke-width="1.5" stroke-linejoin="bevel" />
        </svg>
    </button>

    <swiper class="slider-swiper" :modules="modules" :centered-slides="true" :slides-per-view="quantitySlides" :slides-per-group="1" :space-between="0" :initial-slide="1" :loop="looped" @swiper="onSwiper">

        <swiper-slide v-for="(slider, key) in media" :key='key' @mouseover="addControls" @mouseleave="removeControls">
            <video preload="auto" ref="video" @mouseover="addControls" @mouseleave="removeControls" :class="[{'video-active' : key === activeSlide}, 'video']" v-if="key === activeSlide" :src="slider" muted="muted"></video>
            <video preload="auto" :class="{'video-sleep' : key !== activeSlide}" v-else :src="slider" muted="muted"></video>
        </swiper-slide>
    </swiper>

</div>
</div>
</template>

<script>
export default {
    name: 'SliderVideo'
}
</script>

<script setup>
import {
    onMounted,
    getCurrentInstance,
    ref,
    computed,
    nextTick,
    onUnmounted
} from 'vue';
import {
    Pagination,
    Scrollbar,
    A11y,
    Autoplay
} from 'swiper';
import {
    Swiper,
    SwiperSlide,
    useSwiper
} from 'swiper/vue';
import 'swiper/css/bundle';

let name = ref('SliderVideo')
let modules = [Pagination, Scrollbar, A11y, Autoplay]
let media = ref('')
let swipe = ref('')
let activeSlide = ref(1)
let width = ref('')
let bar = ref('')
let widthWindow = ref('')
let container = ref('')
let nextBtn = ref('')

onMounted(function (app) {
    media.value = [
        'https://www.w3schools.com/html/mov_bbb.mp4',
        'https://storage.googleapis.com/web-dev-assets/video-and-source-tags/chrome.mp4#t=5',
        'https://www.w3schools.com/html/mov_bbb.mp4',
        'https://storage.googleapis.com/web-dev-assets/video-and-source-tags/chrome.mp4#t=5',
        'https://www.w3schools.com/html/mov_bbb.mp4',
        'https://storage.googleapis.com/web-dev-assets/video-and-source-tags/chrome.mp4#t=5',
        'https://www.w3schools.com/html/mov_bbb.mp4',
        'https://storage.googleapis.com/web-dev-assets/video-and-source-tags/chrome.mp4#t=5',
    ]
    playVideo()
    updateWidth()
    window.addEventListener('resize', updateWidth)
});

onUnmounted(function (app) {
    window.removeEventListener('resize', updateWidth)
})

function intersectionObserver() {
    const options = {
        rootMargin: '0px',
        threshold: 0.5
    }
    const callback = function (entries, observer) {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                const video = entry.target
                console.log('video play')
                playVideo()
                observer.unobserve(video)
            }
        });
    };
    const observer = new IntersectionObserver(callback, options);
    const target = container.value;
    observer.observe(target);
}

function updateWidth() {
    widthWindow.value = window.innerWidth;
}

function addControls(event) {
    if (event.target.classList.contains('video-active')) {
        event.target.setAttribute('controls', true)
    }
}

function removeControls(event) {
    const elem = event.target.querySelector('video')
    if (elem) {
        if (elem.classList.contains('video-active')) {
            elem.removeAttribute('controls')
        }
    }

}

const onSwiper = (swiper) => swipe.value = swiper;

// swipe.value.slideTo(2, 500)

function increaseCount() {
    clearProgerss()
    if (activeSlide.value === media.value.length - 1) {
        activeSlide.value = 0
    } else {
        activeSlide.value++
    }
    playVideo()
}

function decreaseCount() {
    clearProgerss()
    if (activeSlide.value === 0) {
        console.log(media.value.length)
        activeSlide.value = media.value.length - 1
    } else {
        activeSlide.value--
    }
    playVideo()
}

function playVideo() {
    nextTick(() => {
        const videoSleep = document.querySelectorAll('.video-sleep')
       
        const video = document.querySelector('.video-active')
        console.log(video.currentTime)
        videoSleep.forEach(video => video.pause())
        video.play()
        tickVideo(video)
    })
}

function tickVideo(vid) {
    vid.addEventListener('loadedmetadata', function () {
        vid.addEventListener('ended', () => {
            swipe.value.slideNext()
            increaseCount()
        })

        vid.addEventListener('timeupdate', updateProgress)

        if (!vid.duration) {
            setTimeout(() => {
                swipe.value.slideNext()
                increaseCount()
            }, 3000);
        }
    });
}

let looped = computed(() => {
    if (media.value.length > 6) {
        return true
    }
    return false
})

let quantitySlides = computed(() => {
    if (widthWindow.value > 650) {
        return 3
    }
    return 1
})

function updateProgress(event) {
    const {
        duration,
        currentTime
    } = event.srcElement
    width.value = (currentTime / duration) * 100
    bar.value.style.width = width.value + "%";
}

function clearProgerss() {
    bar.value.style.width = '0px';
    width.value = 0
}
</script>

